@import url(https://fonts.googleapis.com/css?family=Raleway:400);
@import url(https://fonts.googleapis.com/css?family=Inconsolata);
@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
.page {
    width: 100%;
    position: absolute;
    padding: 48px 32px;
    top: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;
    overflow: hidden;
}

.page__centered {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.page-inner {
    position: -webkit-sticky;
    position: sticky;
    display: inline-block;
    top: 0;
    left: 0;
    -webkit-transform: translate(0px);
            transform: translate(0px);
    -webkit-transition: -webkit-transform 200ms linear;
    transition: -webkit-transform 200ms linear;
    transition: transform 200ms linear;
    transition: transform 200ms linear, -webkit-transform 200ms linear;
}
.description-page {
    background: var(--color-blue-9);
}

.description-page__text-container {
    display: flex;
}

.description-page__text:not(:first-child) {
    margin-left: 48px;
}
.footer-page {
    background: var(--color-grey-9);
    min-height: 100vh;
}

.footer-page__text {
    margin: 8px;
    min-width: unset;
}

.footer-page__link {
    color: var(--color-volcano-5);
}
.main-page {
    background: var(--color-blue-8);
}

.main-page__header {
    margin-bottom: 32px;
}

.main-page__text {
    margin-top: 16px;
}
.panorama-page {
    background: var(--color-grey-7);
    padding: 0;
}

.panorama-page__image {
    width: 4096px;
    height: 971px;
    vertical-align: top;
}

.panorama-page__credits {
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 4px 6px;
    font-size: 12px;
    font-weight: 700;
    line-height: 1.2;
    display: inline-block;
    border-radius: 3px;
    position: absolute;
    right: 32px;
    bottom: 32px;
}

.panorama-page__credits-text {
    display: inline-block;
    padding: 2px 3px;
}
